// app/api/rma/cap-alerts/route.ts
import { NextRequest, NextResponse } from 'next/server';
import { v4 as uuidv4 } from 'uuid';

let capAlerts = [
  {
    id: uuidv4(),
    title: 'Flood Warning',
    description: 'Heavy rain expected in the northern region.',
    severity: 'Severe',
    effective: '2025-08-04T10:00:00Z',
    expires: '2025-08-05T10:00:00Z',
  },
];

export async function GET() {
  return NextResponse.json(capAlerts);
}

export async function POST(req: NextRequest) {
  const data = await req.json();
  const newAlert = { ...data, id: uuidv4() };
  capAlerts.push(newAlert);
  return NextResponse.json({ message: 'CAP alert added', alert: newAlert });
}

export async function DELETE(req: NextRequest) {
  const { id } = await req.json();
  capAlerts = capAlerts.filter((alert) => alert.id !== id);
  return NextResponse.json({ message: 'CAP alert deleted', id });
}
